
<template>
    <section>
       <loader :is-visible="isLoading"></loader>
       <TgList IsShowAction="false"
            ModuleName="CRM" 
            SubModuleCode="CRM_ITEM_TYPES" 
            IdentityColumn="asset_type_id"
            :HeaderText="$t('Product Type')"
            :ListData="ProductTypeData" 
            :HeaderData="Headers"
            :RenderRowActionMethod="renderActions"
            :HeaderButtons="listheaderbuttons" 
            :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields"
            :IsFilterApplied="searchCondition?true:false"
            :ListDataCallBackFunction="FetchData"
            :SortExp="SortExp"
            :SortBy="SortBy"
            :CurrentPageNumber="PageNumber"
            v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'status_id'">
                <listing-status :isfor="'CRM_ITEM_TYPES'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_ITEM_TYPES')" ref="updatestatus" v-bind:dataid="data.row.asset_type_id" data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index" v-bind:dataselected="data.row.status_id" v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true"
                 >
                </listing-status>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_LEADS')">
                    <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                </a>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'View'">
                <td class="text-center single-action tourguide_a">
                    <RouterLink
                     :to="'/CRM/ViewItemTypeDetail/'+data.row.asset_type_id+'/CRM/CRM_ITEM_TYPES'"  
                    style="color: dodgerblue" :typetitle="$t('View')"  v-bind:dataid="data.row.asset_type_id"
                            :title="$t('View')"><em class="fa fa-eye text-info action-icon"></em>
                    </RouterLink>    
                </td>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'Edit'">
                <td class="text-center single-action tourguide_aa">
                    <a v-on:click="UpdateProduct(item)" :typetitle="$t('Edit')" :typefor="Edit"
                            v-bind:dataid="data.row.asset_type_id" :title="$t('Edit')">
                            <em class="fa fa-pencil text-success action-icon"></em>
                        </a>   
                </td>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'attachment_path'">
                <td><img src="/Content/images/imagenotavailable.jpg" width="100" height="50" alt=""></td>
            </template>
        </template>
    </TgList>
        
    <viewlayout v-if="ShowManageView" v-show="ShowManageView" submodulecode="CRM_ITEM_TYPES" modulename="CRM"
            @close="ShowManageView = false" v-bind:callbackfunction="FetchData" v-bind:commonapiurl="commonAPIUrl"
            :usertype="usertype">
            <h5 slot="header">{{$t('Search')}}</h5>
        </viewlayout>
      
        <searchfilter v-if="showFilter" submodulecode="CRM_ITEM_TYPES" modulename="CRM" v-on:close="showFilter = false"
             v-bind:callbackfunction="FetchData"  v-on:FilterData="FilterData">
                <h5 slot="header">{{$t('Search')}}</h5>
            </searchfilter>
    </section>
</template>

<script>
import { RouterLink } from "vue-router";       
    import Schema from "../../CRM/ProductType/ProductTypeSchema";
    import TgList from '../../../components/Common/TgList.vue';
    import ProductTypeDataService from "../../../services/DataService";
    import  viewlayout from '../../../components/Common/Modal/view-layout.vue';
    import searchfilter from '../../../components/Common/Modal/search-filter.vue';
export default {
    components: {
            TgList,
            viewlayout,
            searchfilter
        },
    data(){
        return{
            listheaderbuttons: Schema.buttons,
            userid: null,
            usertype:null,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            noRecord: false,
            TotalRecords: 0,
            searchCondition: '',
            isLoading:false,
            srchcondition:'',
            leftSearchFields: Schema.leftSearchFields,
            Headers: [
                {
                    
                    "COLUMN_NAME":"asset_type",
                    "DATA_TYPE":"string",
                    "DT_CODE":"text",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":1,
                    "DISPLAY_NAME":"asset_type" 
                },
                {
                    "COLUMN_NAME":"description",
                    "DATA_TYPE":"string",
                    "DT_CODE":"textarea",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":2,
                    "DISPLAY_NAME":"description"
                },
                {
                    "COLUMN_NAME":"status_id",
                    "DATA_TYPE":"select",
                    "DT_CODE":"select",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":3,
                    "DISPLAY_NAME":"Status"
                },
                {
                    "COLUMN_NAME":'modified_at',
                    "DATA_TYPE":"date",
                    "DT_CODE":"date",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":4,
                    "DISPLAY_NAME":"MODIFIED_AT"
                },
                {
                    "COLUMN_NAME":"type_code",
                    "DATA_TYPE":"string",
                    "DT_CODE":"text",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":5,
                    "DISPLAY_NAME":"TypeCode"
                },
                {
                    "COLUMN_NAME":"attachment_path",
                    "DATA_TYPE":"string",
                    "DT_CODE":"file",
                    "SORTABLE":false,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":6,
                    "DISPLAY_NAME":"Image"
                },
                {
                    "COLUMN_NAME":"created_by",
                    "DATA_TYPE":"string",
                    "DT_CODE":"text",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":7,
                    "DISPLAY_NAME":"CreatedBy"
                },
                {
                    "COLUMN_NAME":"modified_by",
                    "DATA_TYPE":"string",
                    "DT_CODE":"text",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":9,
                    "DISPLAY_NAME":"ModifiedBy"
                },
                {
                    "COLUMN_NAME":"View",
                    "DATA_TYPE":"string",
                    "DT_CODE":"text",
                    "SORTABLE":false,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":10,
                    "DISPLAY_NAME":""
                },
                {
                    "COLUMN_NAME":"Edit",
                    "DATA_TYPE":"string",
                    "DT_CODE":"text",
                    "SORTABLE":false,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":11,
                    "DISPLAY_NAME":""
                }
            ],
            ProductTypeData: null,
            ShowManageView: false,
            StatusListForListing: [],
            showFilter: false,
            searchCondition: '',
            conditionForInstantSearch: "",
            listheaderbuttons: Schema.buttons,
            itemName:'',
            appliedFilter:[]
        }
    },
    created:function(){
        this.userid = this.GetUserInfo.ID;
          this.usertype = this.GetUserInfo.UserType;
        var VueObj=this;
        VueObj.getStatuslistForListing("Items", "en");
        VueObj.FetchData();
    },
    
    methods:{
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
            },
        getStatuslistForListing: function (StatusType, lang) {
                var vm = this;
                var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
                 
                ProductTypeDataService.statusdropdown(url).then(function (response) {
                    if (response.data.status == "Success") {
                        var json = response.data.result;
                        vm.StatusListForListing = json.data;
                    }
                });
            },
        commonHeaderButtonClick: function (event) {               
            switch (event.callbackfunction) { 
                case "commonDeleteData":
                        this.commonDeleteData();
                        break;  
                    case "ManageView":
                      this.ManageView();
                        break;
                        case "openFilterPopUp":
                        this.openFilterPopUp();
                        break;
                        case "downloadExceldata":
                        this.FetchData(true);
                        break;
                        case "setTourGuideSteps":
                        this.setTourGuideSteps(0);
                        break; 
                        case "removeFilter":
                        var ths = this;
                this.searchCondition = '';
                const removeFilterindex= ths.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });
                ths.listheaderbuttons[removeFilterindex].isvisible=false;
                const openFilterPopUpindex= ths.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                ths.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
                try {
                    ths.appliedFilter = [];
                } catch (e) { };
                ths.FetchData();
                        break;       
            }
        },
        FilterData:function(pageNum,pageSize,sortBy,sortExp,searchcondition,filterItemData){
               this.pageNum=pageNum;
               this.pageSize=pageSize;
               this.sortBy=sortBy;
               this.sortExp=sortExp;
               this.searchCondition=searchcondition;
               if(typeof filterItemData!=null)
               this.appliedFilter.push(filterItemData);
             this.FetchData();
            },
        openFilterPopUp: function (source_id) {
                this.cancelOrRefresh();
                this.showFilter = true;
            },
            cancelOrRefresh: function () {
                this.clientemail = "";
                this.selectedLeadOwner = "";
                this.NameSearch = "";
                this.CompanyName = "";
                this.hdnOwnerId = "";
                this.conditionForInstantSearch = "";
                this.IsAnySearch = false;
                this.FetchData();
            },
        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            params["subModuleCode"]="CRM_ITEM_TYPES";
            params["moduleName"]="CRM";
            return params;
        },
        ManageView: function () {
            var vm = this;
            this.ShowManageView = true;
            $("#modalManageView").modal('show');
            setTimeout(function () {
                $("#modalManageView").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
        },
        commonDeleteData: function (event) {
                    var vm = this;
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " +vm.$t("Item")), function () {
                            var postJSON = {
                                ids: val,
                                moduleName: "CRM",
                                subModuleCode: "CRM_ITEM_TYPES"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            ProductTypeDataService.deleteActivityData(objectPostString).then(function (response) {
                                var box = "";
                                if (response.data[0].Status == "Success") {
                                    $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                  $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                    box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Asset')]), "success", true, vm.$t("Alert"));
                                    vm.PageNumber=parseInt(vm.PageNumber)-1;
                                    if(vm.PageNumber<=0){
                                        vm.PageNumber=1;
                                    } 
                                    vm.FetchData(); 
                                    setTimeout(function () {
                                        box.modal('hide');
                                    }, 3000);
                                }
                                else if (response.data[0].Status == "RecordIsUsed") {
                                    box = vm.ShowAlert(vm.$t('PartialDeletedError', [vm.$t('Asset')]), "success", true, vm.$t("Alert"));
                                    setTimeout(function () {
                                        box.modal('hide');
                                    }, 3000);
                                }
                                else {
                                    box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Asset')]), "failure", true, vm.$t("Alert"));
                                }
                                 
                            }, function (response) {
                                 
                            });
                        });
                    }
                    else {
                        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"),"warning", true, vm.$t("Alert"));
                    }
            },
        FetchData: function(downloadExceldata){
            const params = this.getRequestParams(this.pageNum, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);
            var VueObj = this;
            VueObj.isLoading = true;
            params.pageSize = VueObj.PageSize;
            params.pageNum = VueObj.PageNumber;
            params.searchCondition = VueObj.searchCondition;
            if (VueObj.PageNumber) { params.pageNum = VueObj.PageNumber; }
            else { VueObj.PageNumber = params.pageNum = 1; }
            if (typeof VueObj.isPaged != 'undefined') { params.isPaged = VueObj.isPaged; }
            else { VueObj.isPaged = params.isPaged = 1; }
            params.isPaged = VueObj.isPaged;
            params.sortBy = VueObj.SortBy;
            params.sortExp = VueObj.SortExp;
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) { srchCondition = srchCondition + this.searchCondition; }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                params.searchCondition = srchCondition; 
                if(VueObj.appliedFilter.length>0){
                const removeFilterindex= VueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                VueObj.listheaderbuttons[removeFilterindex].isvisible=true;      
                const openFilterPopUpindex= VueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                VueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter text-success';
            }
            }
                if(downloadExceldata){
                    ProductTypeDataService.ItemTypeListing(params).then(function(response){
                        if (response.data != null) {
                            VueObj.JSONToCSVConvertor(response.data, "Item-Detail", true);
                        }
                        else {
                            VueObj.ModelnoRecord = true;
                        }
                        VueObj.isLoading = false;
                        });
                }else{
                ProductTypeDataService.ItemTypeListing(params).then(function(response){
                    if (response.data != null) {
                        VueObj.Headers.forEach((column) => {
                                column.settings = null;
                                if(column.COLUMN_NAME == 'attachment_path'){
                                    column.settings = {
                                        isInSlot : true
                                    }
                                }
                                if(column.COLUMN_NAME == 'status_id'){
                                    column.settings = {
                                        isInSlot : true
                                    }
                                }
                                if(column.COLUMN_NAME == 'View'){
                                    column.settings = {
                                        isInSlot : true
                                    }
                                }
                                if(column.COLUMN_NAME == 'Edit'){
                                    column.settings = {
                                        isInSlot : true
                                    }
                                }
                            });
                        if (response.data.data.length > 0) {                                  
                            VueObj.ProductTypeData = response.data.data;
                            VueObj.TotalRecords = response.data.data[0].total_records;
                            VueObj.TotalPages = Math.ceil(VueObj.TotalRecords / VueObj.PageSize);
                            VueObj.CurrentPage = VueObj.PageNumber;
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $('#DeleteMultiple').addClass('disabled');
                            VueObj.noRecord = false;
                        } else {
                            VueObj.noRecord = true;
                            VueObj.ProductTypeData = [];
                            VueObj.TotalRecords = 0
                        }
                    }
                    else {
                        VueObj.noRecord = true;
                        VueObj.ProductTypeData = [];
                        VueObj.Headers = [];
                        VueObj.TotalRecords = 0
                    }
                });
                 setTimeout(function(){
                    VueObj.ResponsiveDataTable('tablelistingdata')
                }, 500);
                VueObj.isLoading = false;
            }            
        },
        pagerMethod: function (value) {
               this.PageSize = value.PageSize;
               this.PageNumber = value.PageNumber;
               this.FetchData();
           },
           sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {                    
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                }
               
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
            },
        onEnter: function () {
                this.Searchdata();
            },
            Searchdata: function (event) {
                
                this.PageNumber = 1;
                this.GroupStart = this.PageNumber;
                this.GroupEnd = this.PageNumber + 2;
                this.IsAnySearch = false;
                if ($.trim(this.itemName) != "") {
                    this.IsAnySearch = true;
                    this.searchCondition += ($.trim(this.searchCondition) != "") ? " AND" : "";
                    this.searchCondition += " asset_type  LIKE '%" + this.itemName + "%'";
                }
                this.FetchData();
            },
            cancelOrRefresh: function () {
                this.Item = '';
                this.itemName = '';
                this.searchCondition = '';
                this.IsAnySearch = false;
                this.FetchData();
            },
    }
}

</script>