

<template>
    <div>
    <!--Custom Loader component start-->
    <loader :is-visible="isLoading"></loader>
    <!--Custom Loader component start-->
        <TgList IsShowAction="true"
                ModuleName="CRM" 
                SubModuleCode="CRM_ITEMS" 
                IdentityColumn="asset_catalogue_id"
                :HeaderText="$t('Products')"
                :ListData="ProductData" 
                :HeaderData="Headers"
                :HeaderButtons="listheaderbuttons"
                :TotalRecords="TotalRecords"
                :SearchFields="leftSearchFields"
                :RenderRowActionMethod="renderActions"
                :ListDataCallBackFunction="FetchData"
                :LegendArray="LegendArray"
                :IsFilterApplied="searchCondition?true:false"
                :SortExp="SortExp"
                :SortBy="SortBy"
                :CurrentPageNumber="PageNumber"
                v-on:LeftsearchButtonClick="leftCommonSearch"
                v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick"
                v-on:ActionButtonClick="actionButtonClick"
                v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{data}">
                    <template v-if="data.column.COLUMN_NAME == 'status_id'">
                        <listing-status  :isfor="'CRM_ITEMS'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_ITEMS')" ref="updatestatus" v-bind:dataid="data.row.asset_catalogue_id" data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index" v-bind:dataselected="data.row.status_id" v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                        </listing-status>
                        <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_ITEMS')">
                            <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                                {{data.row[data.column.COLUMN_NAME]}}
                            </span>
                        </a>
                    </template>
                </template>
        </TgList>
        <searchfilter v-if="closeFilter" submodulecode="CRM_ITEMS" modulename="CRM"  v-on:close="closeFilter = false"
             v-bind:callbackfunction="FetchData"  v-on:FilterData="FilterData">
                <h5 slot="header">{{$t('Search')}}</h5>
        </searchfilter>
        <viewlayout v-if="ShowManageView" v-show="ShowManageView" submodulecode="CRM_ITEMS" modulename="CRM"
            @close="ShowManageView = false" v-bind:callbackfunction="FetchData" v-bind:commonapiurl="commonAPIUrl"
            :usertype="usertype">
            <h5 slot="header">{{$t('Search')}}</h5>
        </viewlayout>
    </div>
      </template>

<script>
import viewlayout from '../../../components/Common/Modal/view-layout.vue'
import searchfilter from '../../../components/Common/Modal/search-filter.vue'
import Schema from "./ProductListingSchema";
import ProductDataService from "../../../services/DataService";
import TgList from '../../../components/Common/TgList.vue';

export default {
    components: {
        viewlayout,
        searchfilter,
        TgList
    },
    data() {
        return {
            isLoading: false, 
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            ProductData: null,
            Headers: null,
            listActions: Schema.listActions,
            TotalRecords: 0,
            pageNumber: 1,
            pageSize: 10,
            sortBy: '',
            SortBy: '',
            sortExp: '',
            SortExp:'',
            PageSizeOption: null,
            CurrentPage: 1,
            TotalPages: 0,
            item: null,
            statusId: null,
            subModuleCode: "CRM_ITEMS",
            moduleName: "CRM",
            langCode: null,
            searchCondition: null,
            StatusListForListing:null,
            closeFilter: false,
            ShowManageView: false,
            LegendArray:[],
            LoggedinUserId:null,
            usertype:null, 
            appliedFilter: []
        }
    },
    created: function () {
            var ths = this;
            ths.LoggedinUserId = ths.GetUserInfo.ID;
        ths.usertype = ths.GetUserInfo.UserType;
            ths.getStatuslistForListing("Items", "en");
            ths.FetchData();
            // ths.LegendArray = [
            // {
            //     class: 'legend-pending colorboxsquare',
            //     text: (ths.$t('CloseLeadLegend', ['Product']))
            // }
        // ]
        },
    updated: function () {
        this.BindActionButtonEvent();
    },
    methods: {   
        actionButtonClick:function(){

        },
            renderActions: function(row) {
        var self = this;
        let rtnActionArray = [];
        self.listActions.forEach(actionItem => {
            let action = {...actionItem};
            switch (action.title) {
                case 'View':
                case 'Edit':
                default:
                    action.isVisible = true;
                    break;
                
            }
            rtnActionArray.push(action);
        });
        return rtnActionArray;
    },
    clickEventOfColumn:function(row, column){
        if(column.COLUMN_NAME == 'name' || column.COLUMN_NAME  == 'View'){
            this.$router.push({ path: '/CRM/ViewItemDetails/'+row.asset_catalogue_id+'/CRM/CRM_ITEMS' });
        }
        if(column.COLUMN_NAME == 'Edit'){
            this.$router.push({ path: '/CRM/Manage/'+row.asset_catalogue_id+'/CRM/CRM_CONTACTS' });
        }
    },
        commonHeaderButtonClick: function (event) {
                console.log("buttom click value", event);
                switch (event.callbackfunction) {
                    case "downloadExceldata":
                        this.FetchData(true);
                        break;
                    case "ManageView":
                      this.ManageView();
                        break;                                   
                    case "openFilterPopUp":
                        this.openFilterPopUp();
                        break;
                        case "setTourGuideSteps":
                      this.setTourGuideSteps(0);
                        break; 
                        case "commonDeleteData":
                        this.commonDeleteData();
                        break;  
                        case "removeFilter":
                        var ths = this;
                this.searchCondition = '';
                const removeFilterindex= ths.listheaderbuttons.findIndex(object => {
                     return object.callbackfunction === 'removeFilter';
                    });
                ths.listheaderbuttons[removeFilterindex].isvisible=false;
                const openFilterPopUpindex= ths.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                ths.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
                try {
                    ths.appliedFilter = [];
                } catch (e) { };
                ths.FetchData();
                        break;
                }
            },
        getStatuslistForListing: function (StatusType, lang) {
                var vm = this;
                var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
                 
                ProductDataService.statusdropdown(url).then(function (response) {
                    if (response.data.status == "Success") {
                        var json = response.data.result;
                        vm.StatusListForListing = json.data;
                    }
                });
            },
        getRequestParams: function (item, statusId, subModuleCode, moduleName, sortBy, sortExp, pageSize, pageNumber, langCode, searchCondition) {
            let params = {};
            if (item) { params["item"] = item; }
            if (statusId) { params["statusId"] = statusId; }
            if (subModuleCode) { params["subModuleCode"] = subModuleCode; }
            if (moduleName) { params["moduleName"] = moduleName; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (langCode) { params["langCode"] = langCode; }
            if (searchCondition) { params["searchCondition"] = searchCondition; }
            return params;
        },
        commonDeleteData: function (event) {      
                    var vm = this;
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("CRM_ITEMS")), function () {
                            var postJSON = {
                                ids: val,
                                moduleName: "CRM",
                                subModuleCode: "CRM_ITEMS"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            ProductDataService.CommonDeleteData(objectPostString).then(function (response) {                               
                                var box = "";
                                if (response.status == 200) {
                                    $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                 $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                    $("#DeleteMultiple").addClass("disabled");
                                    box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Product')]),"success", true, vm.$t("Alert")); 
                                    vm.PageNumber=parseInt(vm.PageNumber)-1;
                                    if(vm.PageNumber<=0){
                                        vm.PageNumber=1;
                                    } 
                                    vm.FetchData();  
                                }
                                else {
                                    box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Product')]),"failure", true, vm.$t("Alert"));   ;
                                }
                              
                            }, );
                        });
                    }
                    else {
                        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                    }                
            },
        FetchData: function (downloadExceldata) {
            const params = this.getRequestParams(this.item, this.statusId, this.subModuleCode, this.moduleName, this.sortBy, this.sortExp, this.pageSize, this.pageNumber, this.langCode, this.searchCondition);
            var vueObj = this;
            vueObj.isLoading = true;
            params.item = vueObj.item;
            params.statusId = vueObj.statusId;
            params.subModuleCode = "CRM_ITEMS";
            params.moduleName = "CRM";
            params.langCode = vueObj.langCode;
            if(downloadExceldata){
                params.pageSize = vueObj.TotalRecords;
            }else{ 
                params.pageSize = vueObj.pageSize;
            }
            if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNum = 1; }
            params.sortBy = vueObj.sortBy;
            params.sortExp = vueObj.sortExp;
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) { srchCondition = srchCondition + this.searchCondition; }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                params.searchCondition = srchCondition;
                if (vueObj.appliedFilter.length > 0) {
                    const removeFilterindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vueObj.listheaderbuttons[removeFilterindex].isvisible = true;
                    const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter text-success';
                }
            }
            else{
                if(vueObj.appliedFilter.length==0){
                const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vueObj.listheaderbuttons[removeFilterindex].isvisible=false;      
                const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
            }
          }
            if (downloadExceldata) {
                console.log(params);
                ProductDataService.GetProductList(params)
                    .then(function (response) {
                        if (response.data != "" && response.data != null) {
                            vueObj.JSONToCSVConvertor(response.data, "Product-Detail", true);
                        }
                        else {
                            vueObj.ModelnoRecord = true;
                        }
                        vueObj.isLoading = false;
                    });
            }
            else {
                ProductDataService.GetProductList(params)
                    .then(function (response) {
                     
                        if (response.data != "" && response.data != null) {
                            vueObj.Headers = response.data.schema;
                            vueObj.Headers.forEach((column) => {
                                    column.settings = null;
                                    if(column.COLUMN_NAME == 'name'){
                                        column.settings = {
                                            clickEvent: vueObj.clickEventOfColumn,
                                            formatter: vueObj.columnDataFormatter
                                        }
                                    }
                                    if(column.COLUMN_NAME == 'status_id'){
                                        column.settings = {
                                            isInSlot : true
                                        }
                                    }
                                });
                            if (response.data.data.length > 0) {
                                vueObj.ProductData = response.data.data;
                                vueObj.ProductData.forEach((row)=>{
                                row.rowDetailsUrl='';
                                if(row.rowDetailsUrl=='')
                                {
                                    row.rowDetailsUrl='/CRM/ViewItemDetails/'+row.asset_catalogue_id+'/CRM/CRM_ITEMS';
                                }
                                });
                                vueObj.TotalRecords = response.data.data[0].total_records;
                                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                                vueObj.CurrentPage = vueObj.pageNumber;
                                vueObj.noRecord = false;
                            } else {
                                vueObj.noRecord = true;
                                vueObj.ProductData = [];
                                vueObj.TotalRecords = 0
                            }
                        }
                        else {
                            vueObj.noRecord = true;
                            vueObj.ProductData = [];
                            vueObj.Headers = [];
                            vueObj.TotalRecords = 0
                        }
                       setTimeout(function(){
                                vueObj.ResponsiveDataTable('tablelistingdata')
                            }, 500);
                        vueObj.isLoading = false;
                    });
            }
        },
        sortdata: function (s) {            
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        ReloadStatus: function (s) {
            this.FetchData();
        }, 
        FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData) {
            this.pageNum = pageNum;
            this.pageSize = pageSize;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.searchCondition = searchcondition;
            if (typeof filterItemData != null){
                this.appliedFilter.push(filterItemData);
            }
            this.FetchData();
        },
        openFilterPopUp: function (source_id) {
            this.cancelOrRefresh();
            this.closeFilter = true;
        },
        cancelOrRefresh: function () {
            this.clientemail = "";
            this.selectedLeadOwner = "";
            this.NameSearch = "";
            this.CompanyName = "";
            this.hdnOwnerId = "";
            this.conditionForInstantSearch = "";
            this.IsAnySearch = false;
            this.FetchData();
        },
        ManageView: function () {
           
            this.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal('show');
                setTimeout(function () {
                    SetBootstraptoolTip(); 
                }, 200);
            }, 1000);
          
        },
        RefreshSearch: function (s) {
            this.FetchData();
        },
        leftCommonSearch: function (SearchValue) {
            const params = this.getRequestParams(this.item, this.statusId, this.subModuleCode, this.moduleName, this.sortBy, this.sortExp, this.pageSize, this.pageNumber, this.langCode, this.searchCondition);
            var vueObj = this;
            this.isLoading = true;
            params.item = vueObj.item;
            params.statusId = vueObj.statusId;
            params.subModuleCode = "CRM_ITEMS";
            params.moduleName = "CRM";
            params.langCode = vueObj.langCode;
            params.pageSize = vueObj.pageSize;
            if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNum = 1; }
            params.sortBy = vueObj.sortBy;
            params.sortExp = vueObj.sortExp;
            params.searchCondition = SearchValue.searchCondition;
           
            ProductDataService.GetProductList(params)
                    .then(function (response) {
                        if (response.data != "" && response.data != null) {
                            vueObj.Headers = response.data.schema;
                            vueObj.Headers.forEach((column) => {
                                    column.settings = null;
                                    if(column.COLUMN_NAME == 'name'){
                                        column.settings = {
                                            clickEvent: vueObj.clickEventOfColumn,
                                            formatter: vueObj.columnDataFormatter
                                        }
                                    }
                                    if(column.COLUMN_NAME == 'status_id'){
                                        column.settings = {
                                            isInSlot : true
                                        }
                                    }
                                });
                            if (response.data.data.length > 0) {
                                vueObj.ProductData = response.data.data;
                                vueObj.TotalRecords = response.data.data[0].total_records;
                                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                                vueObj.CurrentPage = vueObj.pageNumber;
                                vueObj.noRecord = false;
                            } else {
                                vueObj.noRecord = true;
                                vueObj.ProductData = [];
                                vueObj.TotalRecords = 0
                            }
                        }
                        else {
                            vueObj.noRecord = true;
                            vueObj.ProductData = [];
                            vueObj.Headers = [];
                            vueObj.TotalRecords = 0
                        }
                        setTimeout(function(){
                                vueObj.ResponsiveDataTable('tablelistingdata')
                            }, 500);
                        vueObj.isLoading = false;
                    });
        }
        
    },  
}
</script>